



















































import ut from '../../utils/index.js';
import marked from '../../components/marked'
import wxParse from '../../components/mpvue-wxparse/src/wxParse.vue'
export default {
	data() {
		return {
			static:'',
			type:0,
			malllist:[
			],
			guigetype:[],
			num: 0,
			mallinf: {},
			mallprice:0,
			barlist:[],
			pop:false,
			clientGoods:{},
			detailinf:'',
			comlist:[],
			pop1:false,
			swipeList:[]
		}
	},
	components: {
		wxParse
	},
	onLoad(opt) {
		this.static=ut.static;
		ut.settitle(opt.title||'商品详情')
		this.req_detail(opt._id)
		this.req_comment(opt._id)
	},
	methods: {
		cgpop(){
			this.cg_pop1();
		},
		cg_pop1(){
			if(!this.comlist.length){
				ut.totast('暂无评价');
				return;
			}
			this.pop1=!this.pop1;
		},
		onShareAppMessage() {
			var that = this;
			return {
				title:'',
				success: (res) => {
					console.log("转发成功", res);
				},
				fail: (res) => {
					console.log("转发失败", res);
				}
			}
		},
		cg_type(type){
			this.type=type;
			this.mallinf =this.guigetype[type]
			if(this.mallinf.picture){
				this.swipeList=this.mallinf.picture.split(',')
			}else{
				this.swipeList=[this.clientGoods.picture]
			}
		},
		cg_pop(){
			if(!this.pop&&!this.num){
				return;
			}
			this.pop=!this.pop;
		},
		go_build_pay(){
			if(!wx.getStorageSync('token')){
				wx.navigateTo({
					url: '../mine/login'
				})
				return
			}
			let barlist=[...this.guigetype];
			barlist=barlist.filter(item=>{
				console.log(item.num)
				return item.num>0
			})
			console.log(JSON.stringify(barlist))
			if(barlist.length){
				barlist=barlist;
			}else{
				const mallinf=Object.assign({},this.mallinf)
				barlist.push(mallinf);
				barlist[0].num=1;
			}
			wx.setStorageSync('buildinf',barlist)
			wx.navigateTo({
				url: `../build/pay?name=${this.clientGoods.name}`
			})
		},
		minus(index){
			console.log(index)
			if(this.num==0)return;
			if(this.guigetype[index].num==0)return;
			this.num-=1;
			this.guigetype[index].num-=1;
			this.mallprice=(Number(this.mallprice)-Number(this.guigetype[index].price)).toFixed(2);
			if(this.num==0){
				this.pop=false;
			}				
		},
		add(index){
			console.log(index)
			this.guigetype[index].num+=1;
			this.num+=1;
			this.mallprice=(Number(this.mallprice)+Number(this.guigetype[index].price)).toFixed(2);
		},
		cleanbar(){
			this.num=0;
			this.mallinf.num=0;
			this.mallprice=0;
			this.barlist[0]=[];
			this.pop=false;
		},
		req_detail(id){
			ut.request({
				data: {
					goodsid:id
				},
				url: "goods/goodsdetail"
			}).then(data=>{
				if(!data.clientGoods){
					ut.totast('商品信息不全')
					return
				}
				this.clientGoods=data.clientGoods||{};
				const reg=new RegExp('/attach/download\\?filePath=','g');
				this.detailinf=marked(this.clientGoods.detail.replace(reg,ut.static));
				if(data.clientGoodsSpecList[0]){
					data.clientGoodsSpecList.forEach(item =>{
						item.num=0;
					})
					this.mallinf =data.clientGoodsSpecList[0]
					if(this.mallinf.picture){
						this.swipeList=this.mallinf.picture.split(',')
					}else{
						this.swipeList=[this.clientGoods.picture]
					}
					
				}
				this.guigetype=data.clientGoodsSpecList;
			})
		},
		filterdate(barlist){
			let parm=[];
			for(let i=0;i<barlist.length;i++){
				parm[i]={
					goodsId:barlist[i].goodsId,
					num:barlist[i].num,
					specId:barlist[i].id
				}
			}
			return parm;
		},
		req_cartadd(){
			if(!wx.getStorageSync('token')){
				wx.navigateTo({
					url: '../mine/login'
				})
				return
			}
			let barlist=[...this.guigetype];
			barlist=barlist.filter(item=>{
				console.log(item.num)
				return item.num>0
			})
			if(!barlist.length){
				ut.totast('请先选择商品');
				return;
			}
			const parm=this.filterdate(barlist);
			ut.request({
				data:parm,
				url: "cart/add",
				c:true
			}).then(data=>{
				this.cleanbar();
				ut.totast('加入购物车成功');
			})
		},
		req_comment(id){
			ut.request({
				data: {
					proId:id,
					type:2
				},
				url: "comment/list"
			}).then(data=>{
				this.comlist=data;
			})
		}
	}
}
